import React, { useState, useEffect } from 'react';
import * as XLSX from 'xlsx';
import './OrderForm.css';

function OrderForm() {
  const [packageTotal, setPackageTotal] = useState(0);
  const [hallTotal, setHallTotal] = useState(0);
  const [orderType, setOrderType] = useState('');
  const [name, setName] = useState('');
  const [tableNumber, setTableNumber] = useState();
  const [numberOfPeople, setNumberOfPeople] = useState('');
  const [money, setMoney] = useState('');
  const [orders, setOrders] = useState([]);
  const [completedOrders, setCompletedOrders] = useState([]);

  useEffect(() => {
    // Load saved orders from local storage on component mount
    const savedOrders = localStorage.getItem('orders');
    if (savedOrders) {
      setOrders(JSON.parse(savedOrders));
    }
  }, []);

  useEffect(() => {
    // Save orders to local storage whenever it changes
    localStorage.setItem('orders', JSON.stringify(orders));
  }, [orders]);

  const handleSubmit = (e) => {
    e.preventDefault();

    // Ensure that the number of people is a valid positive integer
    const parsedNumberOfPeople = parseInt(numberOfPeople, 10);
    if (isNaN(parsedNumberOfPeople) || parsedNumberOfPeople < 1) {
      alert(' 1   ');
      return;
    }  

    const newOrder = {
      orderType: orderType,
      name: name,
      tableNumber: tableNumber,
      numberOfPeople: numberOfPeople,
      money: money
    };
    setOrders([...orders, newOrder]);
    setOrderType('');
    setName('');
    setTableNumber('');
    setNumberOfPeople('');
    setMoney('');

    // Update cumulative number of people for package and hall orders
    if (orderType === '') {
        setPackageTotal(packageTotal + Number(numberOfPeople));
    } else if (orderType === '') {
        setHallTotal(hallTotal + Number(numberOfPeople));
    }
  };

  const handleCompleted = (index, order) => {
    const updatedOrders = [...orders];
    updatedOrders.splice(index, 1);
    setOrders(updatedOrders);

    // Update cumulative number of people for package and hall orders
    if (order.orderType === '') {
        setPackageTotal(packageTotal - Number(order.numberOfPeople));
    } else if (order.orderType === '') {
        setHallTotal(hallTotal - Number(order.numberOfPeople));
    }

    // Move order to completed orders
    setCompletedOrders([...completedOrders, order]);

    // Reset the checkbox to unchecked
    const checkbox = document.getElementById(`checkbox-${index}`);
    if (checkbox) {
      checkbox.checked = false;
    }
  };

  //    
  const saveAsExcel = () => {
    // Create a new workbook
    const workbook = XLSX.utils.book_new();
    const completedOrdersWorksheet = XLSX.utils.json_to_sheet(completedOrders);
    XLSX.utils.book_append_sheet(workbook, completedOrdersWorksheet, ' ');
    // Generate an Excel file in binary form
    const excelData = XLSX.write(workbook, { bookType: 'xlsx', type: 'binary' });
    // Convert binary data to a Blob object
    const blob = new Blob([s2ab(excelData)], { type: 'application/octet-stream' });

    //  
    const date = new Date();
    const year = date.getFullYear().toString().slice(-2);
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const day = date.getDate().toString().padStart(2, '0');

    // Create a download link and trigger the file download
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `_${year}${month}${day}.xlsx`;
    link.click();
  };
  
  // Helper function to convert string to ArrayBuffer
  const s2ab = (s) => {
    const buf = new ArrayBuffer(s.length);
    const view = new Uint8Array(buf);
    for (let i = 0; i < s.length; i++) {
      view[i] = s.charCodeAt(i) & 0xff;
    }
    return buf;
  };  

  return (
    <div>
        <div className='form-container'>
        <form onSubmit={handleSubmit}>
          <div className='question'>
            <label>
              /
              <select value={orderType} onChange={(e) => setOrderType(e.target.value)}>
                <option value=""> </option>
                <option value=""> </option>
              </select>
            </label>
          </div>
          <div className='question'>
            <label>
              
              <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </label>
          </div>   
          <div className='question'>
            <label>
              
              <input
                type="text"
                value={tableNumber}
                onChange={(e) => setTableNumber(e.target.value)}
              />
            </label>
          </div>    
          <div className='question'>
          </div>
          <div className='question'>
            <label>
              
              <input
                type="number"
                value={numberOfPeople}
                onChange={(e) => setNumberOfPeople(e.target.value)}
              />
            </label>
          </div>
          <div className='question'>
            <label>
              
              <input
                type="number"
                value={money}
                onChange={(e) => setMoney(e.target.value)}
              />
            </label>
          </div>     
          <button type="submit"></button>
        </form>
      </div>

      <button onClick={saveAsExcel}>  </button>  

      <div className='order-status'>
        <p className='order-type'>: {packageTotal}</p>
        <p className='order-type'>: {hallTotal}</p>
      </div>

      {orders.length > 0 && (
        <table>
          <caption> </caption>
          <thead>
            <tr>
              <th> / </th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order, index) => (
                <tr key={index}>
                    <td>{order.orderType}</td>
                    <td>{order.name}</td>
                    <td>{order.tableNumber}</td>
                    <td>{order.numberOfPeople}</td>
                    <td>{order.money}</td>
                    <td>
                    <input
                        id = {`checkbox-${index}`}
                        type="checkbox"
                        onChange={() => handleCompleted(index, order)}
                    />
                    </td>
                </tr>
                ))}
          </tbody>
        </table>
      )}

      {completedOrders.length > 0 && (
        <table>
          <caption> </caption>
          <thead>
            <tr>
              <th>/</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {completedOrders.map((order, index) => (
              <tr key={index}>
                <td>{order.orderType}</td>
                <td>{order.name}</td>
                <td>{order.tableNumber}</td>
                <td>{order.numberOfPeople}</td>
                <td>{order.money}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default OrderForm;
